import { Container, Grid, makeStyles, Typography } from '@material-ui/core';
import React from 'react';
import { useAllMembers } from 'src/hooks/members/useAllMembers';
import MemberCard from 'components/Cards/MemberCard';
import deepPurple from '@material-ui/core/colors/deepPurple';
import AnimatedListItem from '../Animated/AnimatedListItem';

const useStyles = makeStyles(({ spacing, typography, breakpoints }) => ({
  root: {
    padding: `${spacing(15)}px 0`,
    [breakpoints.down('md')]: {
      padding: `${spacing(10)}px 0`,
    },
  },
  title: {
    fontWeight: typography.fontWeightBold,
    textAlign: 'center',
    [breakpoints.down('md')]: {
      fontSize: typography.h6.fontSize,
    },
  },
  description: {
    marginBottom: spacing(10),
  },
}));

const MemberSection = () => {
  const styles = useStyles();
  const members = useAllMembers('lubycon');

  return (
    <section className={styles.root}>
      <Container>
        <Typography variant="h4" component="h2" gutterBottom={true} className={styles.title}>
          루비콘 팀의 멤버들이에요
        </Typography>
        <Typography className={styles.description}></Typography>
        <Grid container spacing={2}>
          {members.map(mentor => (
            <Grid item key={mentor.id} xs={6} sm={3}>
              <AnimatedListItem>
                <MemberCard member={mentor} backgroundColor={deepPurple['100']} />
              </AnimatedListItem>
            </Grid>
          ))}
        </Grid>
      </Container>
    </section>
  );
};

export default MemberSection;
