import { Container, Grid, makeStyles, Typography } from '@material-ui/core';
import React, { useRef, useState } from 'react';
import Emoji from 'components/Utils/Emoji';
import { graphql, useStaticQuery } from 'gatsby';
import GatsbyImage from 'gatsby-image';
import AnimatedAngleIcon from '../Animated/AnimatedAngle';
import { FluidImageSharp } from 'src/models/image';
import { useScrollDown } from 'src/hooks/useScrollDown';
import { animated, useSpring, useSprings } from 'react-spring';
import ImpressionArea from '../Utils/ImpressionArea';

const useStyles = makeStyles(({ spacing, typography, breakpoints }) => ({
  root: {
    position: 'relative',
    height: '100vh',
  },
  text: {
    [breakpoints.down('md')]: {
      order: 1,
      paddingTop: 0,
    },
  },
  title: {
    fontWeight: typography.fontWeightBold,
    [breakpoints.down('md')]: {
      fontSize: typography.h6.fontSize,
    },
  },
  description: {
    marginBottom: spacing(10),
  },
  image: {
    width: 550,
    paddingLeft: spacing(10),
    [breakpoints.down('md')]: {
      order: 0,
      width: 300,
      paddingLeft: 0,
    },
  },
  bottomArrow: {
    position: 'absolute',
    bottom: '10%',
    left: '50%',
    transform: 'translateX(-50%)',
  },
}));

const IntroSection = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const [visible, setVisible] = useState(false);
  const styles = useStyles();
  const { thumbnail } = useStaticQuery<{ thumbnail: FluidImageSharp }>(graphql`
    query {
      thumbnail: file(absolutePath: { regex: "/study.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  const titleAnimation = useSpring({
    opacity: visible ? 1 : 0,
    transform: visible ? 'translateX(0)' : 'translateX(-50px)',
  });
  const descriptionAnimation = useSpring({
    opacity: visible ? 1 : 0,
    transform: visible ? 'translateX(0)' : 'translateX(-50px)',
    delay: 300,
  });

  const scrollToBottom = useScrollDown(containerRef);

  return (
    <Container component="section">
      <Grid ref={containerRef} container justify="center" alignItems="center" className={styles.root}>
        <Grid item xs={12} sm className={styles.text}>
          <ImpressionArea onImpressionStart={() => setVisible(true)}>
            <animated.div style={titleAnimation}>
              <Typography variant="h4" component="h2" gutterBottom={true} className={styles.title}>
                안녕하세요. 우리는 루비콘 입니다 <Emoji emoji="👋" name="Hi" />
              </Typography>
            </animated.div>
            <animated.div style={descriptionAnimation}>
              <Typography className={styles.description}>
                루비콘은 국내 IT업계에 선한 영향을 만들어내고 싶은 개발자들과 디자이너들이 모인 팀이에요.
                <br />
                우리는 지속적으로 새로운 것들에 도전하며 실패하고, 그 과정 속에서 배운 것들을 공유해요. 그리고 이런 공유
                활동은 루비콘 팀 뿐만 아니라 팀 외부에서도 이루어지고 있어요.
              </Typography>
            </animated.div>
          </ImpressionArea>
        </Grid>
        <Grid item xs="auto" className={styles.image}>
          <GatsbyImage fluid={thumbnail.childImageSharp.fluid} />
        </Grid>
        <div className={styles.bottomArrow}>
          <AnimatedAngleIcon onClick={scrollToBottom} />
        </div>
      </Grid>
    </Container>
  );
};

export default IntroSection;
