import { Container, Grid, makeStyles, Typography } from '@material-ui/core';
import React, { useState } from 'react';
import { animated, useSpring } from 'react-spring';
import AnimatedListItem from '../Animated/AnimatedListItem';
import BenefitCard from '../Cards/BenefitCard';
import ImpressionArea from '../Utils/ImpressionArea';

const activities = [
  {
    title: '🎓 스터디',
    description: `우리는 관심있는 분야가 있다면 함께 스터디하고 싶은 멤버들을 모아서 자체 스터디를 하고 있어요.\n최근 팀 내에서 가장 핫한 주제는 "나의 성장 뿐 아니라 팀의 성장을 이끌 수 있는 사람이 되는 것"입니다.`,
  },
  {
    title: '🌱 멘토링',
    description: `우리는 멘토링을 통해 아직 성장의 방향을 잡지 못한 분들을 도와드리고 있어요.\n\n우리가 지금까지 쌓아왔던 모든 경험들이 다른 사람들에게 분명 도움이 될 것이라고 믿거든요.`,
  },
  {
    title: '🤝 커뮤니티',
    description: `루비콘 팀이 운영하는 커뮤니티인 루비콘 허브는 함께 프로젝트를 진행하는 팀과 같은 직군이 모인 챕터로 구성되어 있어요.\n많은 사람들이 빠른 정보 공유와 투명한 커뮤니케이션을 통해 성장하기를 바라요.`,
  },
  {
    title: '💻 프로젝트',
    description:
      '루비콘 팀의 프로젝트는 스스로 필요한 제품을 개발하는 것 뿐만 아니라 UI Kit처럼 다른 사람들의 생산성을 높힐 수 있는 제품을 개발하기도 해요.',
  },
];

const useStyles = makeStyles(({ palette, spacing, typography, breakpoints }) => ({
  root: {
    padding: `${spacing(15)}px 0`,
    backgroundColor: palette.grey['100'],
    [breakpoints.down('md')]: {
      padding: `${spacing(10)}px 0`,
    },
  },
  title: {
    fontWeight: typography.fontWeightBold,
    [breakpoints.down('md')]: {
      fontSize: typography.h6.fontSize,
    },
  },
  description: {
    marginBottom: spacing(5),
  },
}));

const ActivitySection = () => {
  const [visible, setVisible] = useState(false);
  const styles = useStyles();

  const titleAnimation = useSpring({
    opacity: visible ? 1 : 0,
    transform: visible ? 'translateX(0)' : 'translateX(-50px)',
  });
  const descriptionAnimation = useSpring({
    opacity: visible ? 1 : 0,
    transform: visible ? 'translateX(0)' : 'translateX(-50px)',
    delay: 300,
  });

  return (
    <section className={styles.root}>
      <Container>
        <ImpressionArea onImpressionStart={() => setVisible(true)}>
          <animated.div style={titleAnimation}>
            <Typography variant="h4" component="h2" gutterBottom={true} className={styles.title}>
              함께 성장하고 공유하기
            </Typography>
          </animated.div>
          <animated.div style={descriptionAnimation}>
            <Typography className={styles.description}>
              루비콘 팀의 이니셔티브는 <strong>성장과 공유</strong>에요. 루비콘 팀은 이 이니셔티브를 달성하기 위한
              여러가지 활동을 하고 있어요.
              <br />
            </Typography>
          </animated.div>
          <Grid container>
            {activities.map(({ title, description }, index) => (
              <Grid key={index} item xs={12} sm={3}>
                <AnimatedListItem>
                  <BenefitCard title={title} description={description} />
                </AnimatedListItem>
              </Grid>
            ))}
          </Grid>
        </ImpressionArea>
      </Container>
    </section>
  );
};

export default ActivitySection;
